@charset "utf-8";

html, body{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.signup-wrap,
.login-wrap{
    margin: 20px 0;
    width: 300px;
    height: 300px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.signup-wrap input,
.login-wrap input{
    border: none;
    border-bottom: 1px solid black;
    width: 80%;
    height: 30px;
    margin: 5px 0;
}
.signup-wrap button,
.login-wrap button{
    border: none;
    outline: none;
    background-color: transparent;
    background-color: black;
    color: white;
    border-radius: 10px;
    width: 100px;
    height: 30px;
    margin-top: 10px;
    transition: 0.3s;
    cursor: pointer;
}
.login-wrap button:hover,
.signup-wrap button:hover{
    background-color: white;
    color: black;
    border: 1px solid black;
}